.container {
  display: flex;
  flex-direction: column;
  height: calc(95vh - 100px); /* Adjusted height */
  max-width: 700px;
  margin-left: auto; /* Move to the right */
  margin-right: 10px; /* Add 5px space from the right */
  /* background-color: beige; */
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding-right: 0px;
  border-radius: 12px;
  margin-top: 110px; /* Adjusted margin-top */
  margin-bottom: 0px;
  
}
main {
  font-family: "Hind Siliguri", sans-serif;
  text-transform: capitalize;
  flex: 1;
  overflow-y: auto;
  padding: 5px;
  display: flex;
  flex-direction: column;
  background-color: beige;
}

.error_message {
  color: red;
  margin-bottom: 10px;
}

/* Desktop CSS remains the same */

/* Desktop CSS remains the same */

@media (max-width: 520px) {
  main {
    padding: 0px;
    margin-bottom: 60px; /* Added margin bottom to create space for the footer */
  }
  .container {
    margin-top: 0;
    height: calc(100vh - 60px); /* Adjusted height to accommodate the footer */
    margin-right: 0;
    margin-bottom: 0;
  }
  .app {
    height: 100vh;
  }
  footer {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 10px;
    background-color: #f5f5f5;
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 800px) {
  main {
    margin-bottom: 25px; /* Added margin bottom to create space for the footer */
  }
  .container {
    margin-top: 0;
    height: calc(100vh - 60px); /* Adjusted height to accommodate the footer */
    margin-right: 0;
    margin-bottom: 0;
  }
  .app {
    height: 100vh;
  }
  footer {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 10px;
    background-color: #f5f5f5;
  }
}