.app {
  position: relative;
  /* height: 100vh; */
  height: 80vh;
}

.icon_container_main {
  position: fixed;
  bottom: 40px;
  right: 30px;
  cursor: pointer;
  color: #0000ff;
  z-index: 999;
}

.search_icon_main {
  fill: #ffffff;
  height: 40px;
  width: 40px;
  background: #3d4ee0;
  border-radius: 100%;
  padding: 10px;
}
.search_icon_main:hover {
  background-color: #2c3aaf; /* Darker shade of blue */
}