.initial_setup {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  padding: 20px;
  /* background-color: beige; */
  border-radius: 8px;
}

.initial_setup__button {
  padding: 8px 24px;
  font-size: 16px;
  background: white;
  color: black;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.initial-setup__button:hover {
  background-color: rgb(245, 215, 159);
}