.message_list {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-right: 0px;
  overflow-y: auto;
  flex: 1;
   background-color: beige; 
}

.message_item {
  margin-bottom: 5px;
  padding: 10px;
  border-radius: 5px;
}

.user_message_container {
  display: flex;
  justify-content: flex-end;
}

.assistant_message_container {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  padding-bottom: 5px;
}



.loading_animation {
  display: flex;
  justify-content: left;
  align-items: left;
  height: 30px;
}

.loading_circle {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #333;
  margin: 0 5px;
  animation: bounce 0.6s infinite alternate;
}

.purple {
  background-color: #9b59b6;
}

.blue {
  background-color: #3498db;
  animation-delay: 0.2s;
}

.green {
  background-color: #2ecc71;
  animation-delay: 0.4s;
}

.loading_circle:nth-child(2) {
  animation-delay: 0.2s;
}

.loading_circle:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes bounce {
  from {
    transform: translateY(0) rotate(0) scale(1);
  }
  to {
    transform: translateY(-15px) rotate(360deg) scale(0.8);
  }
}
